/*--------------------------------------------------------------
# Start Service Area
--------------------------------------------------------------*/
.service-area {
    background: $offwhite;
    padding-bottom: 90px;
    @media(max-width: 991px) {
        padding-bottom: 50px;
    }
}

.single-service {
    padding: 36px 27px;
    text-align: center;
    border: 1px solid $gray;
    background: $white;
    margin-bottom: 30px;
    @include transition();
    @media(max-width: 1199px) and (min-width: 992px) {
        padding: 36px 15px;
    }
    @media(max-width: 768px) {
        padding: 30px 18px;
    }
    .top-icon {
        display: inline-block;
        background: rgba($primary-color, .15);
        border-radius: 50px;
        width: 80px;
        height: 80px;
        text-align: center;
        line-height: 80px;
    }
    h4 {
        font-size: 20px;
        font-weight: 700;
        color: $title-color;
        margin-top: 25px;
        margin-bottom: 18px;
        @media(max-width: 1199px) and (min-width: 992px) {
            font-size: 18px;
        }
    }
    p {
        margin-bottom: 21px;
    }
    &:hover {
        box-shadow: 0px 20px 40px 0 rgba($title-color, 0.16);
        .primary-btn2 {
            color: $primary-color;
            i {
                color: $primary-color;
                left: 15px;
            }
        }
    }
}

/*--------------------------------------------------------------
# End Service Area
--------------------------------------------------------------*/