/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/

.header-top {
    font-size: 12px;
    padding: 0px;
    height: 40px;
    line-height: 40px;
    background-color: #222833;  
    a {
        color: $white;
        font-weight: 500;
        font-size: 12px;
        i {
            color: $gray;
        }
    }
    .header-top-right-list {
        display: flex;
        justify-content: flex-end;
        margin: 0 auto;
        > li {
            &:not(:last-child) {
                a {
                    padding-right: 10px;
                    border-right: 1px solid $gray;
                }
            }
        }
        .nice-select {
            background: transparent;
            border: 0;
            font-size: 12px;
            &:after {
                border-bottom: 2px solid $white;
                border-right: 2px solid $white;
            }
            &:hover {
                .current {
                    color: $primary-color;
                }
            }
            .current {
                color: $white;
                @include transition();
            }
        }
    }
    .header-top-right {
        text-align: right;
        i {
            @include transition();
        }
        a {
            margin-left: 8px;
            @include transition();
        }
        a:hover {
            color: $primary-color;
            i {
                color: $primary-color;
            }
        }
    }
    .header-top-left {
        display: inline-block;
        a {
            margin-right: 30px;
            @include transition();
            &:hover {
                color: $primary-color;
            }
        }
    }
    @media(max-width:991px) {
        display: none;
    }
}

.top-head-btn {
    background: $primary-color;
    padding: 9px 26px;
}

@media(max-width:991px) {
    #mobile-nav-toggle {
        i {
            color: $primary-color !important;
        }
    }
}

.main-menu {
    @media(max-width: 991px) {
        height: 100px;
        line-height: 100px;
    }
    @media(max-width:768px) {
        height: 80px;
        line-height: 80px;
    }
}

#header {
    transition: all 0.5s;
    z-index: 997;
    background: #fff;
    hr {
        margin: 0;
        background: rgba(204, 204, 204, 0.54);
        @media(max-width: 991px) {
            display: none;
        }
    }
}

#header.header-scrolled {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    transition: all 0.5s;
    box-shadow: -21.213px 21.213px 30px 0px rgba(158, 158, 158, 0.3);
    .header-top,
    hr {
        display: none;
    }
}

@media(max-width:673px) {
    #logo {
        margin-left: 20px;
    }
}

#header #logo h1 {
    font-size: 34px;
    margin: 0;
    padding: 0;
    line-height: 1;
    font-weight: 700;
    letter-spacing: 3px;
}

#header #logo h1 a,
#header #logo h1 a:hover {
    color: #000;
    padding-left: 10px;
    border-left: 4px solid $primary-color;
}

#header #logo img {
    padding: 0;
    margin: 0;
}

@media (max-width: 768px) {
    #header #logo h1 {
        font-size: 28px;
    }
    #header #logo img {
        max-height: 40px;
    }
}

/*--------------------------------------------------------------
# Navigation Menu
--------------------------------------------------------------*/

/* Nav Menu Essentials */

.nav-menu,
.nav-menu * {
    margin: 0;
    padding: 0;
    list-style: none;
}

.nav-menu ul {
    position: absolute;
    display: none;
    top: 100%;
    left: 0;
    z-index: 99;
}

.nav-menu li {
    position: relative;
    white-space: nowrap;
}

.nav-menu>li {
    float: left;
}

.nav-menu li:hover>ul,
.nav-menu li.sfHover>ul {
    display: block;
}

.nav-menu ul ul {
    top: 0;
    right: 100%;
}

.nav-menu ul li {
    min-width: 180px;
}

/* Nav Menu Arrows */
.sf-arrows .sf-with-ul {
    padding-right: 30px;
    &:after {
        content: "\e64b";
        position: absolute;
        right: 18px;
        font-family: 'themify';
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
    }
}

.sf-arrows ul .sf-with-ul:after {
    content: "\f105";
}

/* Nav Meu Container */

@media (max-width: 768px) {
    #nav-menu-container {
        display: none;
    }
}

/* Nav Meu Styling */

.nav-menu {
    a {
        margin: 36px 8px;
        text-decoration: none;
        display: inline-block;
        color: $title-color;
        font-weight: 500;
        text-transform: uppercase;
        outline: none;
        font-size: 14px;
        letter-spacing: 0.1px;
        @media (max-width: 1199px) and (min-width: 992px) {
            font-size: 13px;
            margin: 36px 2px;
        }
    }
    .primary-btn {
        padding: 0px 35px;
        margin: 24px 0;
        text-transform: capitalize;
        line-height: 44px;
    }
}

.nav-menu li:hover>a,
.nav-menu>.menu-active>a {
    color: $primary-color;
}

.nav-menu>li {
    margin-left: 18px;
}

.nav-menu ul {
    padding: 10px;
    box-shadow: 0px 6px 16px rgba(127, 137, 161, 0.25);
    background: #fff;
}

.nav-menu ul li {
    transition: 0.3s;
}

.nav-menu ul li a {
    margin: 0;
    padding: 5px 10px;
    color: #333;
    transition: 0.3s;
    display: block;
    font-size: 14px;
    text-transform: none;
}

.nav-menu ul li:hover>a {
    color: $primary-color;
}

.nav-menu ul ul {
    margin-right: 10px;
    margin-top: 0;
}

/* Mobile Nav Toggle */

#mobile-nav-toggle {
    position: fixed;
    right: 15px;
    z-index: 999;
    top: 28px;
    border: 0;
    background: none;
    font-size: 24px;
    display: none;
    transition: all 0.4s;
    outline: none;
    cursor: pointer;
    @media (max-width: 991px) {
        top: 36px;
    }
    @media (max-width: 991px) {
        top: 28px;
    }
}

#mobile-nav-toggle i {
    color: $white;
    font-weight: 900;
}

@media (max-width: 960px) {
    #mobile-nav-toggle {
        display: inline;
    }
    #nav-menu-container {
        display: none;
    }
}

/* Mobile Nav Styling */

#mobile-nav {
    position: fixed;
    top: 0;
    padding-top: 18px;
    bottom: 0;
    z-index: 998;
    background: rgba(0, 0, 0, 0.8);
    left: -260px;
    width: 260px;
    overflow-y: auto;
    transition: 0.4s;
}

#mobile-nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

#mobile-nav ul li {
    position: relative;
}

#mobile-nav ul li a {
    color: #fff;
    font-size: 13px;
    text-transform: uppercase;
    overflow: hidden;
    padding: 10px 22px 10px 15px;
    position: relative;
    text-decoration: none;
    width: 100%;
    display: block;
    outline: none;
    font-weight: 700;
}

#mobile-nav ul li a:hover {
    color: #fff;
}

#mobile-nav ul li li {
    padding-left: 30px;
}

#mobile-nav ul .menu-has-children i {
    position: absolute;
    right: 0;
    z-index: 99;
    padding: 15px;
    cursor: pointer;
    color: #fff;
}

#mobile-nav ul .menu-has-children i.fa-chevron-up {
    color: $primary-color;
}

#mobile-nav ul .menu-has-children li a {
    text-transform: none;
}

#mobile-nav ul .menu-item-active {
    color: $primary-color;
}

#mobile-body-overly {
    width: 100%;
    height: 100%;
    z-index: 997;
    top: 0;
    left: 0;
    position: fixed;
    background: rgba(0, 0, 0, 0.7);
    display: none;
}

/* Mobile Nav body classes */

body.mobile-nav-active {
    overflow: hidden;
}

body.mobile-nav-active #mobile-nav {
    left: 0;
}

body.mobile-nav-active #mobile-nav-toggle {
    color: #fff;
}